import { Formik, Field, Form } from "formik";

import React, { useEffect, useState } from "react";

import styles from "./adminPanel.module.css";

interface Product {
  id: string;
  imageUrl: string;
  name: string;
  typeSize: string;
  size: string;
  barcode: string;
  maker: string;
  brend: string;
  description: string;
  price: string;
  scopeOfApplication: string[];
}

interface Item {
  id: string;
  imageUrl: string;
  name: string;
  typeSize: string;
  size: string;
  barcode: string;
  maker: string;
  brend: string;
  description: string;
  price: string;
  scopeOfApplication: string[];
}

export default function AdminPanel() {
  const [arrayProduct, setArrayProduct] = useState([]);

  const showListProducts = () => {
    const url = new URL("https://641b23c71f5d999a445c652b.mockapi.io/products");
    fetch(url)
      .then((response) => response.json())
      .then((items) => {
        setArrayProduct(items);
        console.log(items, "arrayProduct");
      });
  };

  useEffect(() => {
    showListProducts();
  }, []);

  const addProduct = (product: Product) => {
    fetch("https://641b23c71f5d999a445c652b.mockapi.io/products/", {
      method: "POST",
      headers: { "content-type": "application/json" },
      body: JSON.stringify(product),
    })
      .then((res) => {
        if (res.ok) {
          return res.json();
        }
      })
      .then((task) => {
        showListProducts();
      })
      .catch((error) => {});
  };

  const deleteProduct = (idProduct: string) => {
    fetch(`https://641b23c71f5d999a445c652b.mockapi.io/products/${idProduct}`, {
      method: "DELETE",
    })
      .then((res) => {
        if (res.ok) {
          return res.json();
        }
      })
      .then((task) => {
        showListProducts();
      })
      .catch((error) => {});
  };

  const updateProduct = (idProduct: string, product: Product) => {
    fetch(`https://641b23c71f5d999a445c652b.mockapi.io/products/${idProduct}`, {
      method: "PUT",
      headers: { "content-type": "application/json" },
      body: JSON.stringify(product),
    })
      .then((res) => {
        if (res.ok) {
          return res.json();
        }
      })
      .then((task) => {
        showListProducts();
      })
      .catch((error) => {
        console.log("  ");
      });
  };

  let initialValues = {
    id: "",
    name: "",
    maker: "",
    barcode: "",
    brend: "",
    description: "",
    imageUrl: "",
    price: "",
    scopeOfApplication: [],
    size: "",
    typeSize: "",
  };

  return (
    <div className={styles.adminPanel}>
      <h3>  :</h3>
      <Formik
        initialValues={initialValues}
        onSubmit={(values) => {
          addProduct(values);
        }}
        enableReinitialize
      >
        {({ values, setFieldValue }) => {
          const handleClick = (item: Item) => {
            setFieldValue("id", item.id);
            setFieldValue("name", item.name);
            setFieldValue("maker", item.maker);
            setFieldValue("barcode", item.barcode);
            setFieldValue("brend", item.brend);
            setFieldValue("description", item.description);
            setFieldValue("imageUrl", item.imageUrl);
            setFieldValue("price", item.price);
            setFieldValue("scopeOfApplication", item.scopeOfApplication);
            setFieldValue("size", item.size);
            setFieldValue("typeSize", item.typeSize);
          };
          const updateClick = () => {
            console.log(values, " ");
            updateProduct(values.id, values);
          };

          return (
            <Form className={styles.container}>
              <div className={styles.wrapper}>
                {/* <label htmlFor="id">Id :</label>
                <Field id="id" name="id" placeholder="Id " type="text" /> */}
                <label htmlFor="name"> </label>
                <Field
                  id="name"
                  name="name"
                  placeholder=" "
                  type="text"
                  required
                />
                <label htmlFor="maker">"</label>
                <Field
                  id="maker"
                  name="maker"
                  placeholder=""
                  type="text"
                  required
                />
                <label htmlFor="barcode"></label>
                <Field
                  id="barcode"
                  name="barcode"
                  placeholder=""
                  type="text"
                  required
                />
                <label htmlFor="brend"></label>
                <Field
                  id="brend"
                  name="brend"
                  placeholder=""
                  type="text"
                  required
                />
                <label htmlFor="description"></label>
                <Field
                  id="description"
                  name="description"
                  placeholder=""
                  type="text"
                  required
                />
                <label htmlFor="imageUrl">  </label>
                <Field
                  id="imageUrl"
                  name="imageUrl"
                  placeholder="  "
                  type="text"
                  required
                />
                <label htmlFor="price"></label>
                <Field
                  id="price"
                  name="price"
                  placeholder=""
                  type="number"
                  required
                />
                <label htmlFor="size">/</label>
                <Field
                  id="size"
                  name="size"
                  placeholder="/"
                  type="text"
                  required
                />
                <label htmlFor="typeSize">
                  . : volume  weight
                </label>
                <Field
                  id="typeSize"
                  name="typeSize"
                  placeholder=". : volume  weight"
                  type="text"
                  required
                />

                <label htmlFor="scopeOfApplication">
                    :
                </label>

                <Field
                  component="select"
                  id="scopeOfApplication"
                  name="scopeOfApplication"
                  multiple={true}
                  required
                >
                  <option value="for body">  </option>
                  <option value="for hands">  </option>
                  <option value="for legs">  </option>
                </Field>

                <button type="submit"> </button>
                <button type="reset"> </button>
                <button type="button" onClick={() => updateClick()}>
                   
                </button>
                <p>
                  *   ,  ID 
                  .
                </p>
              </div>

              <div>
                {arrayProduct.map((item: Item) => {
                  return (
                    <div key={item.id} className={styles.wrapperProduct}>
                      <div>{item.name}</div>
                      <button
                        type="button"
                        onClick={() => deleteProduct(item.id)}
                      >
                         
                      </button>
                      <button type="button" onClick={() => handleClick(item)}>
                         
                      </button>
                    </div>
                  );
                })}
              </div>
            </Form>
          );
        }}
      </Formik>
    </div>
  );
}
