import React, { useState } from "react";
import { useLoaderData } from "react-router-dom";
import { useDispatch } from "react-redux";
import { addItem } from "../../redux/slices/cartSlice";

import styles from "./productCard.module.css";
import iconsCart from "../../img/icons_cart.svg";
import iconsDown from "../../img/icons_download.svg";

type Data = {
  id: string;
  name: string;
  price: number;
  imageUrl: string;
  size: string;
  typeSize: string;
  brend: string;
  barcode: string;
  maker: string;
};

export function Product() {
  const data = useLoaderData() as Data;
  const dispatch = useDispatch();

  const { id, name, price, imageUrl, size, typeSize, brend, barcode, maker } =
    data;
  const [count, setCount] = useState(1);

  const onClickAdd = () => {
    const product = {
      id,
      name,
      price,
      imageUrl,
      count,
      size,
      typeSize,
      brend,
    };

    dispatch(addItem(product));
  };

  const onClickPlus = () => {
    setCount(count + 1);
  };

  const onClickMinus = () => {
    count > 2 ? setCount(count - 1) : setCount(1);
  };

  return (
    <div className={styles.container}>
      <div className={styles.path}>
        <span></span>
        <span></span>
        <span>
          {brend} {name}
        </span>
      </div>
      <div className={styles.wrapper}>
        <div className={styles.imgWrapper}>
          <img src={imageUrl} alt="card_image" />
        </div>

        <div className={styles.description}>
          <span className={styles.stock}> </span>
          <span className={styles.name}>
            <b>{brend} </b>
            <span>{name}</span>
          </span>
          <span className={styles.size}>
            {size} {typeSize === "weight" ? "" : ""}
          </span>
          <div className={styles.bye_block}>
            <span className={styles.price}>{price} </span>
            <div>
              <button onClick={onClickMinus}>-</button>
              <span>{count}</span>
              <button onClick={onClickPlus}>+</button>
            </div>
            <div onClick={onClickAdd}>
              <span> </span>
              <img src={iconsCart} alt="cart" />
            </div>
          </div>
          <div className={styles.promo}>
            <span></span>
            <span>
                 <b>10 000 </b>  <br />  
                
            </span>
            <span>
              - <img src={iconsDown} alt="down" />
            </span>
          </div>
          <span>
            : <span>{maker}</span>
          </span>
          <span>
            : <span>{brend}</span>
          </span>
          <span>: 460404</span>
          <span>
            -  : <span>2</span>
          </span>
          <span>: {barcode}</span>
          <span> :</span>
          <span> :</span>
          <span> </span>
          <span> </span>

          <div className="button-bye"></div>
        </div>
      </div>
    </div>
  );
}
